/* eslint-disable no-underscore-dangle */
import { Component, Input, OnInit, AfterViewInit } from '@angular/core';
import { ModalController } from '@ionic/angular';
import { ApiService } from 'src/app/services/api.service';
import { ToastController } from '@ionic/angular';
import { CommonService } from 'src/app/services/common.service';

@Component({
  selector: 'app-view-tenant',
  templateUrl: './view-tenant.page.html',
  styleUrls: ['./view-tenant.page.scss'],
})
export class ViewTenantPage implements OnInit, AfterViewInit {
  @Input() tenant;
  submitted = false;
  toggleChecked = false;
  showForm = false;
  role;
  user;
  constructor(
    public modalController: ModalController,
    private api: ApiService,
    public toastController: ToastController,
    private common: CommonService,
  ) {}

  ngOnInit() {}

  ionViewDidEnter() {
    this.role = JSON.parse(localStorage.getItem('role'));
    this.user = this.common.getUser();
  }

  ngAfterViewInit() {
    this.toggleChecked = this.tenant.hasCar;
  }

  closeModal() {
    this.modalController.dismiss({
      dismissed: true,
    });
  }

  async presentToast(msg, toastClass) {
    await this.toastController
      .create({
        message: msg,
        cssClass: toastClass,
        duration: 2000,
      })
      .then((res) => res.present());
  }

  deleteSelectedTenant(id) {
    const conf = confirm('Are you sure you want to delete this tenant?');
    if (conf) {
      this.api.deleteTenant('tenant/deleteTenant/' + id).subscribe((res) => {
        if (res) {
          this.presentToast('Tenant Deleted Successfully', 'success');
          setTimeout(() => {
            window.location.reload();
          }, 2200);
        }
      });
    }
  }

  updateTenant(form) {
    this.submitted = true;
    if (form.invalid) {
      this.presentToast('Please fill in all fields!', 'failed');
      return false;
    } else {
      console.log(form.value);

      this.api
        .updateTenant('tenant/updateTenant/' + this.tenant._id, form.value)
        .subscribe((res) => {
          if (!res?.err) {
            this.presentToast('Tenant Updated Successfully', 'success');
            setTimeout(() => {
              window.location.reload();
            }, 2200);
          } else {
            this.presentToast(res?.err, 'failed');
          }
        });
    }
  }
}
